import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { trigger, state, style, animate, transition} from '@angular/animations';

import { AlertService } from '../../services';

@Component({
    selector: 'alertBanner',
    templateUrl: 'alertBanner.utility.html',
    animations: [
      trigger('insertRemoveTrigger', [
        transition(':enter', [
          style({transform: 'translateY(-100%)'}),
          animate('300ms ease-in', style({transform: 'translateY(0%)', opacity: 1}))
        ]),
        transition(':leave', [
          animate('300ms ease-out', style({transform: 'translateY(-100%)', opacity: 0}))
        ])
      ])
    ]
})

export class AlertBannerUtility implements OnInit, OnDestroy {
  private subscription: Subscription;
  dismissible = true;
  alerts: any = [];

  constructor(private alertBannerService: AlertService) { }

  ngOnInit() {
    this.subscription = this.alertBannerService.getMessage().subscribe((message) => {
      console.log('error', message);

      if (message != null) {
        console.log('message exist');
        this.alerts.push(message);
        setTimeout(() => this.alerts.pop(), 5000);
      }

    });

    // this.alerts.push({
    //   type: 'success',
    //  text: `<strong>Well done!</strong> You successfully read this important alert message.`
    // });

    console.log(this.alerts);

  }

  clearAlert() {
    this.alerts.pop();
    // setTimeout(() => this.message = '', 500);
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }
}
