import { Injectable } from '@angular/core';
import { Query, Mutation } from 'apollo-angular';
import gql from 'graphql-tag';
import { SessionDetail } from '../graphqls';

export interface CheckinDetail {
  sessionID: string;
  status: string;
  userID: string;
  expiration: number;
  checkinID: string;
}

export interface CheckinResponse {
  checkinDetail: CheckinDetail;
  sessionDetail: SessionDetail;
}

@Injectable({
  providedIn: 'root',
})
export class GetCheckinGQL extends Query<CheckinResponse> {
  document = gql`
    query GetUser($pinEntry: String!) {
      checkinDetail(pin: $pinEntry) {
        sessionToken
        status
        userID
        expiration
        checkinID
      }
      sessionDetail {
        sessionToken
        status
        userID
        expiration
      }
    }`;
}
