import { Component, OnInit } from '@angular/core';
import { Observable, Subscription } from 'rxjs';
import { map } from 'rxjs/operators';
import { NumberPadService, AlertService, SpinnerOverlayService } from '../services';
import { CheckinDetail, GetCheckinGQL } from '../graphqls';
/**
 * Not Found page
 */
@Component({
  templateUrl: 'qrCheckin.page.html',
})

export class QRCheckinPage {
  private subscription: Subscription
  entry: any;
  padType = 'pin';
  checkinDetail: Observable<CheckinDetail>;
  loading = false;

  constructor(private numberPadService: NumberPadService,
              private getCheckinGQL: GetCheckinGQL,
              private alertService: AlertService,
              private spinnerOverlayService: SpinnerOverlayService) {
  }

  ngOnInit() {
    this.subscription = this.numberPadService.getEntry().subscribe((entry) => {
      this.entry = entry; 
      console.log(this.entry.text)
      console.log("loading")
      this.loading = true;
      this.spinnerOverlayService.show();
      this.checkinLookup();
    })
    
  }
  
  checkinLookup() {
    if (this.entry != null) {
      this.checkinDetail = this.getCheckinGQL.watch({
        pinEntry: this.entry.text
      })
      .valueChanges
      .pipe(
        map(result => result.data.checkinDetail)
      );
    }
    
    this.checkinDetail.subscribe(val => {
      console.log(val);
      console.log("complete loading")
      this.spinnerOverlayService.hide();
      this.loading = false;
      if (val.status == "Unauthorized") {
        this.alertService.error("Invalid PIN")
      }
    })
  }
}