import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { BsModalService, BsModalRef } from 'ngx-foundation';

import { WaitingModal } from '../shared/modals';

@Injectable()
export class WaitingService {
  /**
   * ignore
   */
  bsModalRef: BsModalRef;

  /**
   * Component constructor
   */
  constructor(
    private bsModalService: BsModalService,
  ) { }

  /**
   * Open Login Modal Component and subscribe to modal action.
   */

  open(): void {
    const initialState = {
      class: 'small waiting'
    };
    this.bsModalRef = this.bsModalService.show(WaitingModal, { initialState });
  }

  close(): void {
    this.bsModalRef.hide();

  }
}