import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { QRCheckinPage, QRCheckinStep2Page, NotFoundPage } from './pages';

const routes: Routes = [
  {path: 'qrcheckin', component: QRCheckinPage, data: {title: 'Checkin'}},
  {path: 'step2', component: QRCheckinStep2Page, data: {title: 'LF Checkin'}},
  {path: '404', component: NotFoundPage, data: {title: '404'}},
  {path: '**', redirectTo: '/404'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
